<template>
  <div class="grid grid-cols-1 lg:grid-cols-2 xl:grid-cols-3 gap-4">
    <Product :value="product" v-for="product in productList" :key="product.id" />
  </div>
</template>

<script>
  import { mapState, mapGetters } from 'vuex';

  export default {
    name: "ProductList.vue",

    components: {
      Product: () => import('../components/Product')
    },

    watch: {
      '$route.query.category'(to, from) {
        this.productList = to
          ? this.$store.getters.productList.filter(item => item.category === to)
          : this.$store.getters.productList
      }
    },

    computed: {
      ...mapState(['cartList']),
      // ...mapGetters(['productList']),
    },

    data() {
      return {
        productList: []
      }
    },

    mounted() {
      this.productList = this.$route.query.category
        ? this.$store.getters.productList.filter(item => item.category === this.$route.query.category)
        : this.$store.getters.productList
    }
  }
</script>

